  color: #00AEE9;
  text-align: left;
  background-color: #ffffff
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

html {
  background-color: #ffffff;
}

.App-banner {
  margin-top: 7vh;
  margin-bottom: 7vh;
  justify-content: center;
  display: flex;
  flex-direction: row;
  max-height: 10vh;
  color: #1B295E;
}

.App-header {
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #1B295E;
}

.App-logo {
  width: 10vh;
  height: auto;
  pointer-events: none;
}

.App-title {
  margin-left: 1.5vh;
  font-size: 3vh;
  font-weight: 1;
  color: #00AEE9;
}

.App-link {
  color: #61dafb;
}

.Commands {
  /* margin-top: 10vh; */
  margin-bottom: 6vh;
  display: flex;
  flex-direction: row;
}

.Toastify__toast-container {
  width: 400px;
}

.App-footer a {
  color: #61dafb;
  text-decoration: none;
}

@media only screen and (orientation: landscape) {
  .App-footer {
    margin-top: 6vh;
    font-size: 1.2vh;
  }
}

@media only screen and (orientation: portrait) {
  .App-footer {
    margin-top: 6vw;
    font-size: 1.2vw;
  }
}

.BackupButton {
  border: none;
  color: white;
  border-radius: 12px;
  opacity: 1;
  transition: 0.3s;
  padding-left: 0;
  padding-right: 0;
}

@media only screen and (orientation: landscape) {
  .BackupButton {
    width: 15vw;
    height: 10vw;
    font-size: 3vw;
  }
}

@media only screen and (orientation: portrait) {
  .BackupButton {
    width: 15vh;
    height: 10vh;
    font-size: 3vh;
  }
}

.BackupButton:disabled {opacity: 0.5}

.BackupButton:hover:enabled {opacity: 0.8}
/**
* ----------------------------------------------
* Demo styles
* ----------------------------------------------
**/

.accordion {
    text-align: left;
    font: Monaco,Consolas,"Andale  Mono","DejaVu Sans Mono",monospace;
    border: 5px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
  }

@media only screen and (orientation: landscape) {
  .accordion {
    width: 70vw;
    font-size: 1.2vw;
  }
}

@media only screen and (orientation: portrait) {
  .accordion {
    width: 90vw;
    font-size: 1.2vh;
  }
}


.accordion__item + .accordion__item {
    border-top: 5px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
}

.accordion__button {
    background-color: #758796;
    color: #f4f4f4;
    cursor: pointer;
    padding: 18px;
    text-align: left;
    border: none;
    transition: 0.3s;
}

.accordion__button:hover {
    background-color: #758796;
}

.accordion__button:before {
    display: inline-block;
    content: '';
    height: 1vh;
    width: 1vh;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

@media only screen and (orientation: landscape) {
  .accordion__button:before {
    height: 1vw;
    width: 1vw;
  }
}

@media only screen and (orientation: portrait) {
  .accordion__button:before {
    height: 1vh;
    width: 1vh;
  }
}

.accordion__button[aria-expanded='true']::before,
.accordion__button[aria-selected='true']::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

[hidden] {
    display: none;
}

.accordion__panel {
    padding: 20px;
    -webkit-animation: fadein 0.35s ease-in;
            animation: fadein 0.35s ease-in;
}

code {
    color: #00AEE9;
}

a {
  color: #00AEE9;
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

